module.exports = "<!--\r\n1. CSS 动画\r\n2. CSS 过渡\r\n-->\r\n\r\n\r\n<template>\r\n    <div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>1. CSS 动画</h4>\r\n            <div id=\"example-2\">\r\n                <button @click=\"show = !show\">Toggle show</button>\r\n                <transition name=\"bounce\">\r\n                    <p v-if=\"show\">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris facilisis enim\r\n                        libero, at lacinia diam fermentum id. Pellentesque habitant morbi tristique senectus et\r\n                        netus.</p>\r\n                </transition>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Provide} from 'vue-property-decorator'\r\n\r\n\r\n    @Component\r\n    export default class CSSAnimations extends Vue {\r\n        show = true\r\n    }\r\n</script>\r\n\r\n<style>\r\n    .bounce-enter-active {\r\n        animation: bounce-in .5s;\r\n    }\r\n\r\n    .bounce-leave-active {\r\n        animation: bounce-in .5s reverse;\r\n    }\r\n\r\n    @keyframes bounce-in {\r\n        0% {\r\n            transform: scale(0);\r\n        }\r\n        50% {\r\n            transform: scale(1.5);\r\n        }\r\n        100% {\r\n            transform: scale(1);\r\n        }\r\n    }\r\n</style>";